import { h } from 'preact';
import style from './style';

const Card = ({cardTitle, cardText, cardImg}) => (

	<div class={style.card}>	
		<h1>{cardTitle}</h1>
		<p>{cardText}</p>
		<img class={style.pic} src={cardImg} />
	</div>
	
);

export default Card;




